.button {
  padding: 1rem 2rem;
  border: solid 3px $bronze-light;
  font-family: $italic-bold;

  &:hover {
    text-decoration: none;
    border: solid 3px $bronze-dark;
    color: $bronze-dark;
    transition: all 0.4s ease-in-out;
  }
}



